html, body, #root .App{
  height: 100%;
  margin: 0;
}

.App {
  min-height: 100%;
}

.Header-container {
  display: flex;
  flex-direction: row;
  justify-content: center; 
  align-items: center;     
  gap: 0.2rem;               
}

.App-logo {
  height: 5vmin;
  pointer-events: none;
}

.Shop-icon {
  height: 5vmin; 
  pointer-events: none;
}

.App-header {
  background-color: #4B5060; 
  min-width: fit-content;
  height: 3.125rem;           
  display: flex;
  align-items: center;       
  justify-content: flex-start; 
  font-size: calc(0.75rem + 2vmin);
  color: white;
  padding: 0 1.25rem;         
  border-top-left-radius: 0;   
  border-top-right-radius: 0;  
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
}

.App-shop {
  background-color: #4B5060; 
  min-width: fit-content;
  height: 3.125rem;           
  display: flex;
  align-items: center;       
  justify-content: flex-start; 
  font-size: calc(0.75rem + 2vmin);
  color: white;
  padding: 0 1.25rem;         
  border-top-left-radius: 0;   
  border-top-right-radius: 0;  
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
}

.App-about {
  background-color: #4B5060; 
  min-width: fit-content;
  height: 3.125rem;           
  display: flex;
  align-items: center;       
  justify-content: flex-start; 
  font-size: calc(0.75rem + 2vmin);
  color: white;
  padding: 0 1.25rem;         
  border-radius: 0.3125rem;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
}

.App-products {
  background-color: #4B5060; 
  width: 12rem;               
  height: 3.125rem;
  display: flex;
  align-items: center;       
  justify-content: center;  
  margin-top: 2.5rem;
  margin-left: 1.25rem;
  font-size: calc(0.75rem + 2vmin);
  color: white;
  padding: 0 0.5rem;         
  border-radius: 0.3125rem;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
}

.App-button {
  background-color: #4B5060; 
  width: 3.125rem;
  height: 3.125rem;
  display: flex;
  align-items: center;       
  justify-content: center; 
  font-size: calc(0.25rem + 2vmin);
  font-weight: bold;
  color: white;
  padding: 0 1.25rem;
  border-radius: 0.3125rem;
  border-color: #4B5060;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

#about {
  font-size: calc(0.375rem + 2vmin);
  color: black;
  text-align: left;
  margin-top: 1.5625rem;
  margin-left: 1.25rem;
}

.App-contact {
  width: auto;
  height: 2.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;       
  justify-content: flex-start; 
  font-size: calc(0.75rem + 2vmin);
  color: white;
  padding: 0 1.25rem;
}

.App-main {
  font-size: calc(0.5rem + 2vmin);
  color: black;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 1.25rem;
}

.App-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 80rem; 
}

.Phone-icon,
.Facebook-icon,
.Email-icon,
.Location-icon {
  width: 1.5rem;
  height: 1.5rem;
}

.App-footer {
  width: 100%;
  background: #4B5060;
  color: white;
  padding: 1rem;
  text-align: left;   
}

#footer {
  text-align: center; 
  font-size: small;
  margin-top: 1rem;
}

.Contact-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: 400px;
  align-items: flex-start; 
}

.Contact-line {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
}

.Contact-line p {
  margin: 0;
}

.Contact-line a {
  text-decoration: none;
  color: white;
}

.console-header {
  width: 11.2rem;
  height: 4.125rem;
  font-size: calc(0.75rem + 2vmin);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 1.25rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  color: white;
  box-shadow: 0 0.3125rem 0.3125rem rgba(0, 0, 0, 0.2);
}

.wpp-contact,
.cart {
  position: fixed;
  bottom: 1.25rem;
  right: 1.25rem;
  z-index: 1000;
}

.wpp-icon,
.cart-icon {
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  max-width: 60px;
  height: auto;
}

.wpp-icon:hover,
.cart-icon:hover {
  transform: scale(1.1);
}

.product-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  max-width: 900px;
  margin: 0 auto;
  padding: 0 1rem;
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  width: 150px;           
  display: flex;
  flex-direction: column;
  align-items: center;    
}

.product-image {
  width: 150px;
  height: 150px;
  border-radius: 5px;
  object-fit: cover;
}

.product-title {
  margin-top: 0.5rem;
  max-width: 100%;
  word-wrap: break-word;
  white-space: normal;
  text-align: center;
}

.buy-button {
  margin-top: 0.5rem;
  background-color: #28a745;
  color: white;
  padding: 0.5rem 1rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;             
  box-sizing: border-box;  
}

.buy-button:hover {
  background-color: #218838;
}


@media (max-width: 1024px) {
  body, html, #root, .App {
    min-height: 100%;
  }

  .Header-container {
    display: flex;
    flex-direction: column;   
    justify-content: center;
    align-items: center;      
    gap: 0.25rem;             
    margin-top: 0;            
  }

  .App-header {
    width: auto;
    max-width: 20rem;
    height: 3.125rem;
    justify-content: center;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0.3125rem;
    border-bottom-right-radius: 0.3125rem;
    margin: 0;
    padding: 0 1rem;
  }

  .App-shop {
    width: auto;
    max-width: 20rem;
    height: 3.125rem;
    justify-content: center;
    border-radius: 0.3125rem;
    margin: 0;
    padding: 0 1rem;
  }

  .App-about,
  .App-products {
    width: auto;
    max-width: 18rem;
    justify-content: flex-start;
    margin-left: 1rem;
  }

  .App-products {
    width: 10rem;
    padding: 0 0.5rem;
    margin-top: 1rem;
  }

  .App-footer {
    margin-top: auto; 
    text-align: center;
  }

  .product-container {
    max-width: 700px;
    padding: 0 0.75rem;
    gap: 0.8rem;
  }

  .product-card {
    width: 130px;          
  }

  .product-image {
    width: 130px;
    height: 130px;
  }

  .buy-button {
    padding: 0.25rem 0.5rem;
    font-size: 0.8rem;
  }
  .console-header {
    width: 8.5rem;            
    height: 3.5rem;
    font-size: calc(0.7rem + 1.5vmin);
  }
}

@media (max-width: 600px) {
  body, html, #root, .App {
    min-height: 100%;
    font-size: 14px;
  }

  .Header-container {
    display: flex;
    flex-direction: column;   
    justify-content: center;
    align-items: center;      
    gap: 0.5rem;
    margin-top: 0;
  }

  .App-header {
    width: auto;
    max-width: 16rem;
    height: 3.125rem;
    justify-content: center;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0.3125rem;
    border-bottom-right-radius: 0.3125rem;
    margin: 0;
    padding: 0 1rem;
  }

  .App-shop {
    width: auto;
    max-width: 16rem;
    height: 3.125rem;
    justify-content: center;
    border-radius: 0.3125rem;
    margin: 0;
    padding: 0 1rem;
  }

  .App-about,
  .App-products {
    width: auto;
    max-width: 18rem;
    justify-content: flex-start;
    margin-left: 0.5rem;
  }

  .App-products {
    width: 6rem;
    padding: 0 0.5rem;
    margin-top: 0.75rem;
  }

  .App-footer {
    margin-top: auto;
    align-items: flex-start;
    text-align: left;
  }

  .product-container {
    max-width: 100%;
    padding: 0 0.5rem;
    gap: 0.5rem;
  }

  .product-card {
    width: 120px;           
  }

  .product-image {
    width: 120px;
    height: 120px;
  }

  .buy-button {
    padding: 0.2rem 0.4rem;
    font-size: 0.7rem;
  }

  .console-header {
    width: 7rem;               
    height: 3rem;
    font-size: calc(0.65rem + 1vmin);
  }
}

.product-detail {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 2rem;
  max-width: 1000px;
  margin: 2rem auto;
  padding: 1rem;
}

.product-detail-image {
  flex-shrink: 0;
}

.product-detail-image img {
  width: 300px;
  height: 300px;
  border-radius: 5px;
  object-fit: cover;
  display: block;
}

.product-detail-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.product-header h2 {
  font-size: 1.5rem;
}

.product-price {
  color: #00aa00;
  font-weight: bold;
  margin: 0;
  font-size: 1.3rem;
}

.product-description-title {
  margin: 0.5rem 0 0.25rem;
  font-weight: bold;
}

.product-description {
  margin-bottom: 1rem;
}

.product-includes h4 {
  margin: 0.5rem 0 1rem; 
  font-weight: bold;
}

.product-includes p {
  margin: 0 0 1rem;
}

.product-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.buy-button,
.cart-button,
.back-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  color: white;
}

.buy-button {
  background-color: #28a745;
}

.buy-button:hover {
  background-color: #218838;
}

.cart-button {
  background-color: #007bff;
}

.cart-button:hover {
  background-color: #0056b3;
}

.back-button {
  background-color: #4B5060;
}

.back-button:hover {
  background-color: #333844;
}

@media (max-width: 768px) {
  .product-detail {
    flex-direction: column;
    align-items: center;
  }

  .product-detail-image img {
    width: 80%;
    max-width: 300px;
    height: auto;
  }

  .product-detail-info {
    width: 100%;
  }

  .product-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .product-price {
    margin-top: 0.5rem;
    text-align: left;
  }
}




